import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import { BootstrapVue } from 'bootstrap-vue'
import AnimateCSS from "animate.css";
import VueGraph from 'vue-graph'
import Chartkick from 'vue-chartkick'
import Chart from 'chart.js'

import Trend from "vuetrend"
import TrendChart from "vue-trend-chart";

Vue.use(TrendChart);

Vue.use(Trend)
Vue.use(Chartkick.use(Chart))
Vue.use(VueGraph)

Vue.use(BootstrapVue)
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import './assets/styles/styles.css'
import './assets/font/flaticon.css'
import './assets/font/Flaticon.eot'
import './assets/font/Flaticon.svg'
import './assets/font/Flaticon.ttf'
import './assets/font/Flaticon.woff'
import './assets/font/Flaticon.woff2'


import "../node_modules/flat-icons/ecommerce.css"
import "../node_modules/flat-icons/interface.css"
import "../node_modules/flat-icons/technology.css"
import "../node_modules/flat-icons/creative.css"
Vue.use(AnimateCSS);



Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
