<template>
  <div class="">
    <ul class="tabHeader">
      <li v-for="(tab, index) in tabs" 
      :key="tab.title" 
      @click='selectTab(index)'
      :class='{ tabsSelected: (index === selectedIndex)}'>
        {{ tab.title }}
      </li>
    </ul>
    <slot></slot>
  </div>
</template>
<script>
export default {
  data() {
    return {
      selectedIndex: 0,
      tabs: [],
    };
  },
  methods: {
selectTab(i){
    this.selectedIndex = i
    console.log(i)
    this.tabs.forEach((tab, index) => {
        tab.isActive = (index === i)
    })
}
  },
  created() {
    this.tabs = this.$children;
  },
  mounted () {
    this.selectTab(0)
},
};
</script>
<style scoped>
.tabHeader {
    display: flex;
    list-style: none;
    justify-content: space-around;
    background: white;
    width: 100%;
        height: 36px;
    border-bottom-right-radius: 12px;
    border-bottom-left-radius: 12px;
        padding: 0px;
}
.tabsSelected {
    font-weight: bold;
   
  }
</style>