      :class="{ sent: sent }"
      class="formDiv"
    >
      <Tabs>
        <Tab title="All Members">
          <b-row class="mbersRow">
            <ul class="membersContainter">
              <li
                v-for="(mbr, index) in memberName"
                :key="index"
                @click="updateName(index, mbr), selectMbr(index)"
              >
                <MembersCard
                  :name="mbr.profile.name"
                  :image="mbr.profile.img"
                  v-model="mbrName"
                  :data="mbr"
                  :class="{ activeMbr: index === activeItem }"
                ></MembersCard>
              </li>
            </ul>
          </b-row>
        </Tab>
        <Tab title="My Faves">
          <b-row class="mbersRow">
            <ul class="membersContainter">
              <li
                v-for="(mbr, index) in favorites"
                :key="index"
                @click="updateName(index, mbr), selectMbr(index)"
              >
                <Favorites
                  :name="mbr.profile.name"
                  :image="mbr.profile.img"
                  v-model="mbrName"
                  :data="mbr"
                  :class="{ activeMbr: index === activeItem }"
                ></Favorites>
              </li>
            </ul> </b-row
        ></Tab>
        <Tab title="Accounts">
          <b-row class="mbersRow">
            <ul class="membersContainter">
              <li
