                :key="index"
                v-bind="acctStyle(index)"
                @click="updateCardAcct(index, account), selectAcctCard(index)"
              >
                <AccountCardMd
                  :lastFour="account.cardNumber4"
                  :type="account.purpose"
                  :balance="account.balance"
                  v-model="accountCardSelected"
                  :class="{
                    lightbluedarkblue: index === activeAcct1,
                    lightbluegreen: index === activeAcct2,
                    pinkpurple: index === activeAcct3,
                    orangered: index === activeAcct4,
                    purplepink: index === activeAcct5,
                    bluegreengreen: index === activeAcct6,
                    greengrey: index === activeAcct7,
                    activeCardAcct: index === activeCardAcct,
                  }"
                ></AccountCardMd>
              </li>
            </ul> </b-row
        ></Tab>
      </Tabs>

      <!-- <b-form-group id="amount" label="Amount" label-for="amount">
        <b-form-input
          v-model="amount"
          type="number"
          required
          placeholder="100"
        ></b-form-input>
      </b-form-group> -->
      <div class="purposeInputDiv">
        <div class="purposeInputLabel">Amount</div>

        <input type="text" placeholder="100" v-model="amount" />
      </div>

      <div class="purposeInputDiv">
        <div class="purposeInputLabel">Purpose</div>

        <input type="text" placeholder="Payment" v-model="purpose" />
      </div>
      <b-row class="accountsContainerDiv">
        <ul class="accountsContainer">
          <li
