<script>
import AccountCardSm from "./AccountCardSm.vue";
import AccountCardMd from "./AccountCardMd.vue";
import MembersCard from "../components/MembersCard.vue";
import Favorites from "../components/Favorites.vue";
import members from "../data/members.js";
import accounts from "../data/accounts.js";
import Receipt from "./Receipt";
import Tabs from "./Tabs";
import Tab from "./Tab";

export default {
  name: "TransactionForm",
  components: {
    MembersCard,
    AccountCardSm,
    AccountCardMd,
    Receipt,
    Tabs,
    Tab,
    Favorites,
  },
  data() {
    return {
      sendTo: "all",
      back: false,
      sent: false,
      activeItem: null,
      activeAcct2: null,
      activeAcct3: null,
      activeAcct4: null,
      activeAcct5: null,
      activeAcct6: null,
      activeAcct7: null,
      activeAcct1: null,
      activeAcct: null,
      activeCardAcct: null,
      memberName: members,
      accounts: accounts,
      accountCardSelected: "",
      accountName: "",
      mbrName: "",
      purpose: "",
      amount: "",
      accountSelected: "",
      today: "",
      test: "",
      show: true,
      showRe: false,
ticketNumber: '',
      form: {
        amount: "",
        name: "",
        food: null,
        checked: [],
      },
    };
  },
  computed: {
    favorites() {
      return this.$store.getters.favorites;
    },
    
  },
  methods: {
    ticketCal() {
this.ticketNumber = Math.ceil(Math.random() * 1000) 
    },
    dateNow() {
this.today = new Date().toLocaleDateString() ;
// this.today = new Date().toTimeString().slice(0,9) ;

    },
    selectMbr(index) {
      this.activeItem = index;
    },
    selectAcct(index) {
      this.activeAcct = index;
    },
    selectAcctCard(index) {
      this.activeCardAcct = index;
    },
    updateName(index, mbr) {
      this.mbrName = mbr.profile.name;
    },
    acctStyle(index) {
      if (index == 0) {
        this.activeAcct1 = index;
      } else if (index == 1) {
        this.activeAcct2 = index;
      } else if (index == 2) {
        this.activeAcct3 = index;
      } else if (index == 3) {
        this.activeAcct4 = index;
      } else if (index == 4) {
        this.activeAcct5 = index;
      } else if (index == 5) {
        this.activeAcct6 = index;
      } else if (index == 6) {
        this.activeAcct7 = index;
      }
    },
    // send data of small account
    updateAccount(index, account) {
      this.accountSelected = account.cardNumber4;
    },
    // send data of big account card
    updateCardAcct(index, account) {
      this.accountCardSelected = account.cardNumber4;
    },

    onSubmit() {
      console.log(this.accountSelected, this.amount, this.mbrName);
      this.dateNow()
      this.ticketCal()
      this.showRe = true;
    },
    //  @reset="onReset"
    // onReset(evt) {
    //   evt.preventDefault();
    //   // Reset our form values
    //   this.form.amount = "";
    //   this.form.name = "";
    //   this.form.food = null;
    //   this.form.checked = [];
    //   // Trick to reset/clear native browser form validation state
    //   this.show = false;
    //   this.$nextTick(() => {
    //     this.show = true;
    //   });
    // },
  },
};
</script>

