<template>
  <div id="dashApp">
    <b-col >
      <b-row class="dashHeaderDiv">
        <div class="dashHeader" >Dashboard</div>
        <div class="dashHeader">
          <div
       
           
          >
            <AccountForm></AccountForm>
          </div>
        </div>
      </b-row>
      <b-row >
        <ul class="accountUl">
          <li
            v-for="(account, index) in accounts"
            :key="index"
            v-bind="selectMbr(index)"
          >
            <AccountCard
              :class="{
                lightbluedarkblue: index === activeItem,
                lightbluegreen: index === activeItem2,
                pinkpurple: index === activeItem3,
                orangered: index === activeItem4,
                purplepink: index === activeItem5,
                bluegreengreen: index === activeItem6,
                greengrey: index === activeItem7,
              }"
              :balance="account.balance"
              :cardNumber1="account.cardNumber1"
              :cardNumber2="account.cardNumber1"
              :cardNumber3="account.cardNumber1"
              :cardNumber4="account.cardNumber1"
            >
            </AccountCard>
          </li>
        </ul>
      </b-row>

      <b-row >
        <Table></Table>
      </b-row>
    </b-col>
  </div>
</template>

<script>
import AccountCard from "../components/AccountCard";
import AccountForm from "../components/AccountForm";
import Table from "../components/Table.vue";
import accounts from "../data/accounts";

export default {
  name: "Dashboard",
  components: {
    AccountCard,
    Table,
    AccountForm,
  },
  data() {
    return {
      open: false,
      accounts: accounts,
      bgColor: "#ffffff",
      color1: "#fbd786",
      color2: "#ff3b41",
      angle: "80",
      red: null,
      blue: null,
      purple: null,
      activeItem: null,
      activeItem2: null,
      activeItem3: null,
      activeItem4: null,
       activeItem5: null,
      activeItem6: null,
      activeItem7: null,
      
      modelOpen: false,
    };
  },
  methods: {
    selectMbr(index) {
      console.log(index);
      if (index == 0) {
        this.activeItem = index;
      } else if (index == 1) {
        this.activeItem2 = index;
      } else if (index == 2) {
        this.activeItem3 = index;
      }
      else if (index == 3) {
        this.activeItem4 = index;
      } else if (index == 4) {
        this.activeItem5 = index;
      } else if (index == 5) {
        this.activeItem6 = index;
      }
      else if (index == 6) {
        this.activeItem7 = index;
      }
    },
  },
};
</script>

